// Áî®ÊñºÈô§ÈåØÔºö‰øùÂ≠ò loading screen ÁöÑÂºïÁî®ÂíåÊéßÂà∂ÊñπÊ≥ï
let loadingScreenInstance = null;

export const resourcesLoading = (callback) => {
    const loadingScreen = document.querySelector(".loading-screen");
    const loadingText = document.getElementById("loading-text");

    if (!loadingScreen) {
        gsap.delayedCall(0.5, () => {
            if (typeof callback === 'function') {
                callback();
            }
        });
        return null;
    }

    const images = Array.from(document.images);
    const videos = Array.from(document.querySelectorAll("video"));
    const resources = [...images, ...videos]; // Áµ±‰∏ÄË≥áÊ∫êÈô£Âàó
    const totalResources = resources.length;
    console.log(resources);
    console.log(totalResources);
    let loadedResources = 0;
    let currentDisplayPercent = 0; // ËøΩËπ§Áï∂ÂâçÈ°ØÁ§∫ÁöÑÁôæÂàÜÊØî
    let progressTween = null; // ËøΩËπ§Áï∂ÂâçÁöÑÂãïÁï´ÂØ¶‰æã

    // Êõ¥Êñ∞ÁôæÂàÜÊØîÂáΩÊï∏
    function updateProgress() {
        let targetPercent =
            totalResources === 0
                ? 100
                : Math.floor((loadedResources / totalResources) * 100);
        // console.log(targetPercent);

        // Â¶ÇÊûúÂ∑≤Á∂ìÊúâÂãïÁï´Âú®ÈÅãË°åÔºåÂÖàÂÅúÊ≠¢ÂÆÉ
        if (progressTween) {
            progressTween.kill();
        }

        // ÂâµÂª∫ÂãïÁï´Â∞çË±°
        const progressObj = { value: currentDisplayPercent };

        // ‰ΩøÁî® GSAP ÂãïÁï´ÂæûÁï∂ÂâçÂÄºÂà∞ÁõÆÊ®ôÂÄº
        progressTween = gsap.to(progressObj, {
            value: targetPercent,
            duration: 0.5,
            ease: "power1.out",
            onUpdate: function () {
                const displayValue = Math.floor(progressObj.value);
                if (loadingText) {
                    loadingText.textContent = displayValue;
                }
                currentDisplayPercent = displayValue;
            },
            onComplete: function () {
                progressTween = null;
            }
        });

        if (targetPercent >= 100) {
            // „ÄêÈô§ÈåØÊ®°Âºè„ÄëËÆÄÂèñÂÆå‰∏çÊ∂àÂ§± - Ë®ªËß£ÊéâÈö±ËóèÂãïÁï´
            // Á≠âÂæÖÂãïÁï´ÂÆåÊàêÂæåÂÜçÂü∑Ë°åÈö±ËóèÂãïÁï´
            gsap.delayedCall(0.5, () => {
                gsap.timeline()
                    .to(loadingScreen, {
                        duration: 1,
                        opacity: 0,
                        ease: "power3.in"
                    })
                    .to(
                        loadingScreen,
                        { duration: 1, display: "none", ease: "power1.inOut" },
                        "<0.5"
                    );

                if (typeof callback === 'function') {
                    callback();
                }

            });

            // „ÄêÈô§ÈåØÊ®°Âºè„ÄëËÆÄÂèñÂÆåÊàêÂæåÂè™Âü∑Ë°å callbackÔºå‰∏çÈö±Ëóè loading screen
            // gsap.delayedCall(0.5, () => {
            //     if (typeof callback === 'function') {
            //         callback();
            //     }
            // });
        }
    }

    // Áµ±‰∏ÄÁõ£ËÅΩÂáΩÊï∏
    function listenResourceLoad(res) {
        // Â∑≤Á∂ìÂä†ËºâÂÆåÊàêÊàñÂèØÊí≠Êîæ
        if (
            (res.tagName === "IMG" && res.complete) ||
            (res.tagName === "VIDEO" && res.readyState >= 3)
        ) {
            loadedResources++;
            updateProgress();
        } else {
            res.addEventListener("load", () => {
                loadedResources++;
                updateProgress();
            });
            res.addEventListener("loadeddata", () => {
                loadedResources++;
                updateProgress();
            });
            res.addEventListener("error", () => {
                loadedResources++;
                updateProgress();
            });
        }
    }

    // Áõ£ËÅΩÊâÄÊúâË≥áÊ∫ê
    resources.forEach((res) => listenResourceLoad(res));

    // Â¶ÇÊûúÊ≤íÊúâË≥áÊ∫êÔºå‰πüÁõ¥Êé•Ë∑≥Âà∞ 100%
    if (totalResources === 0) updateProgress();

    // „ÄêÈô§ÈåØÊ®°Âºè„ÄëËøîÂõûÊéßÂà∂ÊñπÊ≥ïÔºåÊñπ‰æøÂõûÊ∫ØÂíåÈô§ÈåØ
    const controller = {
        // È°ØÁ§∫ loading screen
        show: () => {
            if (loadingScreen) {
                gsap.set(loadingScreen, { display: "flex", opacity: 1 });
            }
        },
        // Èö±Ëóè loading screen
        hide: () => {
            if (loadingScreen) {
                gsap.timeline()
                    .to(loadingScreen, {
                        duration: 1,
                        opacity: 0,
                        ease: "power3.in"
                    })
                    .to(
                        loadingScreen,
                        { duration: 1, display: "none", ease: "power1.inOut" },
                        "<0.5"
                    );
            }
        },
        // ÈáçÁΩÆÈÄ≤Â∫¶
        reset: () => {
            loadedResources = 0;
            currentDisplayPercent = 0;
            if (loadingText) {
                loadingText.textContent = 0;
            }
            if (progressTween) {
                progressTween.kill();
                progressTween = null;
            }
        },
        // Ë®≠ÁΩÆÈÄ≤Â∫¶ÁôæÂàÜÊØîÔºàÁî®ÊñºÊ∏¨Ë©¶Ôºâ
        setProgress: (percent) => {
            if (loadingText) {
                loadingText.textContent = Math.floor(percent);
            }
            currentDisplayPercent = percent;
        },
        // Áç≤ÂèñÁï∂ÂâçÁãÄÊÖã
        getState: () => {
            return {
                loadedResources,
                totalResources,
                currentPercent: currentDisplayPercent,
                targetPercent: totalResources === 0 ? 100 : Math.floor((loadedResources / totalResources) * 100)
            };
        },
        // Áç≤Âèñ loading screen ÂÖÉÁ¥†
        element: loadingScreen
    };

    // ‰øùÂ≠òÂØ¶‰æãÂà∞ÂÖ®Â±ÄËÆäÊï∏ÔºåÊñπ‰æøÂú®ÊéßÂà∂Âè∞Ë™øÁî®
    loadingScreenInstance = controller;
    
    // Â∞áÊéßÂà∂Âô®ÊéõËºâÂà∞ window ‰∏äÔºåÊñπ‰æøÂú®ÊéßÂà∂Âè∞Èô§ÈåØ
    if (typeof window !== 'undefined') {
        window.loadingScreenController = controller;
        console.log('„ÄêÈô§ÈåØÊ®°Âºè„ÄëLoading screen ÊéßÂà∂Âô®Â∑≤ÊéõËºâÂà∞ window.loadingScreenController');
        console.log('ÂèØÁî®ÊñπÊ≥ïÔºö');
        console.log('  - window.loadingScreenController.show() - È°ØÁ§∫ loading screen');
        console.log('  - window.loadingScreenController.hide() - Èö±Ëóè loading screen');
        console.log('  - window.loadingScreenController.reset() - ÈáçÁΩÆÈÄ≤Â∫¶');
        console.log('  - window.loadingScreenController.setProgress(50) - Ë®≠ÁΩÆÈÄ≤Â∫¶ÁôæÂàÜÊØî');
        console.log('  - window.loadingScreenController.getState() - Áç≤ÂèñÁï∂ÂâçÁãÄÊÖã');
    }

    return controller;
};
