/**
 * Viewport ÁÆ°ÁêÜÊ®°ÁµÑ
 * ËôïÁêÜË¶ñÁ™óÂ∞∫ÂØ∏Ë®àÁÆó„ÄÅÈüøÊáâÂºèÊñ∑ÈªûÂàáÊèõ
 */

class Viewport {
    constructor() {
        this.vw = 0;
        this.dvh = 0;
        this.currentContext = null;
        this.breakpoint = 1024; // ÊâãÊ©üÁâà/Ê°åÈù¢ÁâàÊñ∑Èªû

        // Ë®≠Ë®àÁ®øÂü∫Ê∫ñÂØ¨Â∫¶
        this.baseWidth = {
            pc: 1920,
            sp: 768
        };

        this.contextChangeEvent = null;

        this.init();
    }

    /**
     * ÂàùÂßãÂåñ
     */
    init() {
        // Ë®àÁÆóÂàùÂßãË¶ñÁ™óÂ∞∫ÂØ∏
        this.updateViewportSize();

        // Ë®≠ÁΩÆÂàùÂßã context
        this.setCurrentContext();

        // ÂâµÂª∫ contextChange ‰∫ã‰ª∂
        this.createContextChangeEvent();

        // Áõ£ËÅΩË¶ñÁ™óÂ§ßÂ∞èÊîπËÆä
        this.bindResizeEvent();

        // Â∞á rpx ÂáΩÊï∏ÊéõËºâÂà∞ window
        window.rpx = this.rpx.bind(this);

        // Ê®ôË®òÁÇ∫Â∑≤Ê∫ñÂÇô
        document.documentElement.classList.add("vwready");

        console.log("‚úÖ Viewport initialized:", {
            vw: this.vw,
            dvh: this.dvh,
            context: this.currentContext
        });
    }

    /**
     * Êõ¥Êñ∞Ë¶ñÁ™óÂ∞∫ÂØ∏
     */
    updateViewportSize() {
        // 1. Ë®àÁÆó‰∏¶ÂÑ≤Â≠òË¶ñÁ™óÂØ¨Â∫¶
        this.vw = document.body.clientWidth;
        window.vw = this.vw;

        // Ë®≠ÁΩÆ CSS Ëá™ÂÆöÁæ©Â±¨ÊÄßÔºåÂèØÂú® CSS ‰∏≠‰ΩøÁî® var(--vw)
        document.documentElement.style.setProperty("--vw", `${this.vw}px`);

        // 2. Ë®àÁÆó Large Viewport Height (ÁßªÂãïÁ´ØÂ§ßË¶ñÁ™óÈ´òÂ∫¶)
        const tempDiv = document.createElement("div");
        tempDiv.style.height = "100dvh";
        tempDiv.style.position = "fixed";
        document.body.append(tempDiv);

        this.dvh = tempDiv.offsetHeight;
        window.dvh = this.dvh;

        tempDiv.remove();

        // Ë®≠ÁΩÆ CSS Ëá™ÂÆöÁæ©Â±¨ÊÄß
        document.documentElement.style.setProperty("--dvh", `${this.dvh}px`);
    }

    /**
     * Ë®≠ÁΩÆÁï∂ÂâçÁí∞Â¢É (pc/sp)
     */
    setCurrentContext() {
        const newContext =
            document.body.clientWidth < this.breakpoint ? "sp" : "pc";
        const isContextChanged =
            this.currentContext && this.currentContext !== newContext;

        this.currentContext = newContext;
        window.currentContext = newContext;

        // Â¶ÇÊûúÁí∞Â¢ÉÊîπËÆäÔºåËß∏Áôº‰∫ã‰ª∂‰∏¶ÈáçÊñ∞ËºâÂÖ•È†ÅÈù¢
        if (isContextChanged) {
            window.dispatchEvent(this.contextChangeEvent);
            console.log("üîÑ Context changed to:", newContext);
            location.reload();
        }
    }

    /**
     * ÂâµÂª∫ contextChange ‰∫ã‰ª∂
     */
    createContextChangeEvent() {
        // ÂÖºÂÆπ IE ÁöÑÂØ´Ê≥ï
        if (document.documentMode) {
            this.contextChangeEvent = document.createEvent("Event");
            this.contextChangeEvent.initEvent("contextChange", true, true);
        } else {
            this.contextChangeEvent = new Event("contextChange");
        }
    }

    /**
     * Á∂ÅÂÆö resize ‰∫ã‰ª∂
     */
    bindResizeEvent() {
        let resizeTimer = null;

        window.addEventListener(
            "resize",
            () => {
                // Èò≤ÊäñËôïÁêÜ
                if (resizeTimer) {
                    clearTimeout(resizeTimer);
                }

                resizeTimer = setTimeout(() => {
                    this.updateViewportSize();
                    this.setCurrentContext();
                    resizeTimer = null;
                }, 100);
            },
            false
        );
    }

    /**
     * ÈüøÊáâÂºèÂÉèÁ¥†ËΩâÊèõÂáΩÊï∏
     * Â∞áË®≠Ë®àÁ®øÂÉèÁ¥†ÂÄºËΩâÊèõÁÇ∫ÂØ¶ÈöõÈüøÊáâÂºèÂÉèÁ¥†
     *
     * @param {number} designPx - Ë®≠Ë®àÁ®ø‰∏äÁöÑÂÉèÁ¥†ÂÄº
     * @param {string} context - 'pc' Êàñ 'sp'Ôºå‰∏çÊèê‰æõÂâá‰ΩøÁî®Áï∂Ââç context
     * @returns {number} ËΩâÊèõÂæåÁöÑÂØ¶ÈöõÂÉèÁ¥†ÂÄº
     *
     * @example
     * rpx(100) // Âú® 1920px ÂØ¨ÁöÑ PC ‰∏ä ‚Üí 133.33px
     * rpx(100, 'sp') // Âº∑Âà∂‰ΩøÁî®ÊâãÊ©üÁâàÂü∫Ê∫ñ ‚Üí 110.4px
     */
    rpx(designPx, context) {
        const targetContext = context || this.currentContext;
        const baseWidth = this.baseWidth[targetContext];
        return (designPx * this.vw) / baseWidth;
    }

    /**
     * Áç≤ÂèñÁï∂ÂâçË¶ñÁ™óË≥áË®ä
     */
    getViewportInfo() {
        return {
            width: this.vw,
            height: this.dvh,
            context: this.currentContext,
            isMobile: this.currentContext === "sp"
        };
    }
}

// ÂâµÂª∫ÂñÆ‰æã
const viewport = new Viewport();

export { viewport };
